import React, { Component } from 'react';
import { IoIosArrowDown, IoIosArrowUp } from 'react-icons/io';
import cssVar from 'views/mobileView/styles';
import FloatingTextInput from '../../atoms/FloatingTextInput';

const defaultItemValue = {
	name: '',
	id: -1
};

export default class SearchableDropDown extends Component {
	constructor(props) {
		super(props);
		this.renderTextInput = this.renderTextInput.bind(this);
		this.renderFlatList = this.renderFlatList.bind(this);
		this.searchedItems = this.searchedItems.bind(this);
		this.renderItems = this.renderItems.bind(this);
		this.state = {
			item: {},
			listItems: [],
			focus: false,
			loading: false
		};
	}

	renderFlatList() {
		if (this.state.focus) {

			return (
				<span style={{ ...this.props.itemsContainerStyle, overflowY: "scroll", display: "flex", flexDirection: "column", fontSize:'14px' }}>
					<ul style={{
						listStyleType: 'none', paddingLeft: 0
					}}>
						{this.state.listItems.map((data) => {
							return <li onClick={() => {
								this.setState({ item: data, focus: false });
								// Keyboard.dismiss();
								setTimeout(() => {
									this.props.onItemSelect(data);
									if (this.props.resetValue) {
										this.setState({ focus: true, data: defaultItemValue });
										this.input.focus();
									}
								}, 0);
							}} style={this.props.itemStyle} key={data.id}>{data.name}</li>
						})}
					</ul>
				</span>
			);
		}
	}

	componentDidMount = () => {
		const listItems = this.props.items;
		const defaultIndex = this.props.defaultIndex;
		// console.log("hey",defaultIndex)
		if (defaultIndex !== undefined && listItems.length > defaultIndex) {
			this.setState({
				listItems,
				item: listItems[defaultIndex],
				loading: true
			});
		} else {
			this.setState({ listItems, loading: true });
		}
	};

	searchedItems = (searchedText) => {
		let setSort = this.props.setSort;
		if (!setSort && typeof setSort !== 'function') {
			setSort = (item, searchedText) => {
				return item.name.toLowerCase().indexOf(searchedText.toLowerCase()) > -1;
			};
		}
		var ac = this.props.items.filter((item) => {
			return setSort(item, searchedText);
		});
		let item = {
			id: -1,
			name: searchedText
		};
		this.setState({ listItems: ac, item: item });
		const onTextChange =
			this.props.onTextChange ||
			this.props.textInputProps.onTextChange ||
			this.props.onChangeText ||
			this.props.textInputProps.onChangeText;
		if (onTextChange && typeof onTextChange === 'function') {
			setTimeout(() => {
				onTextChange(searchedText);
			}, 0);
		}
	};

	renderItems = (item, index) => {
		if (this.props.multi && this.props.selectedItems && this.props.selectedItems.length > 0) {
			return this.props.selectedItems.find((sitem) => sitem.id === item.id) ? (
				<span style={{ ...this.props.itemStyle, flex: 1, flexDirection: 'row' }}>
					<div style={{ flex: 0.9, flexDirection: 'row', alignItems: 'flex-start' }}>
						{item.name}
					</div>
					<div style={{ flex: 0.1, flexDirection: 'row', alignItems: 'flex-end' }}>
						<div
							onClick={() =>
								setTimeout(() => {
									this.props.onRemoveItem(item, index);
								}, 0)}
							style={{
								backgroundColor: '#f16d6b',
								alignItems: 'center',
								justifyContent: 'center',
								width: 25,
								height: 25,
								borderRadius: 100,
								marginLeft: 10
							}}
						>
							X
						</div>
					</div>
				</span>
			) : (
				<span
					onClick={() => {
						this.setState({ item: item });
						setTimeout(() => {
							this.props.onItemSelect(item);
						}, 0);
					}}
					style={{ ...this.props.itemStyle, flex: 1, flexDirection: 'row' }}
				>
					<div style={{ flex: 1, flexDirection: 'row', alignItems: 'flex-start' }}>
						{item.name}
					</div>
				</span>
			);
		} else {
			return (
				<span
					style={{ ...this.props.itemStyle }}
					onClick={() => {
						this.setState({ item: item, focus: false });
						// Keyboard.dismiss();
						setTimeout(() => {
							this.props.onItemSelect(item);
							if (this.props.resetValue) {
								this.setState({ focus: true, item: defaultItemValue });
								this.input.focus();
							}
						}, 0);
					}}
				>
					{this.props.selectedItems &&
						this.props.selectedItems.length > 0 &&
						this.props.selectedItems.find((x) => x.id === item.id) ? (
						<span style={{ ...this.props.itemTextStyle }}>{item.name}</span>
					) : (
						<span style={{ ...this.props.itemTextStyle }}>{item.name}</span>
					)}
				</span>
			);
		}
	};

	// renderListType = () => {
	// 	return this.renderFlatList();
	// };

	renderTextInput = () => {
		// console.log(this.props.textInputProps)
		const input = React.createRef();
		const textInputProps = {};
		const oldSupport = [
			{ key: 'ref', val: (e) => (this.input = e) },
			{
				key: 'onChange',
				val: (text) => {
					this.searchedItems(text.target.value);
				}
			},
			{
				key: 'onFocus',
				val: () => {
					this.props.onFocus && this.props.onFocus();
					this.setState({
						focus: true,
						item: defaultItemValue,
						listItems: this.props.items
					});
				}
			},
			{
				key: 'onBlur',
				val: () => {
					this.props.onBlur && this.props.onBlur(this);
					this.setState({
						focus: false,
						item: this.props.selectedItems
					});
				}
			},
			{
				key: 'value',
				val: this.state.item.name !== undefined ? this.state.item.name : ''
			},


			{
				key: 'placeholder',
				val: this.props.placeholder
			},

			{
				key: 'label',
				val: this.props.label
			}
		];
		oldSupport.forEach((kv) => {
			if (!Object.keys(textInputProps).includes(kv.key)) {
				if (kv.key === 'onChange' || kv.key === 'onChange') {
					textInputProps['onChange'] = kv.val;
				} else {
					textInputProps[kv.key] = kv.val;
				}
			} else {
				if (kv.key === 'onChange' || kv.key === 'onChange') {
					textInputProps['onChange'] = kv.val;
				}
			}
		});

		let items = this.props.selectedItems || [];
		// console.log(this.props)

		return (
			<FloatingTextInput
				{...textInputProps}
				height={this.props.height}
				onBlur={(e) => {
					if (this.props.onBlur) {
						this.props.onBlur(e);
					}
					if (this.props.textInputProps && this.props.textInputProps.onBlur) {
						this.props.textInputProps.onBlur(e);
					}
					this.setState({
						// focus: false,
						item: this.props.selectedItems
					});
				}}
				right={
					this.state.focus ? <IoIosArrowUp
						style={{ alignSelf: 'center', marginLeft: 10, marginBottom: 5,color:cssVar.getPropertyValue('--secondaryGreen') }}
						size={15}
					/> : <IoIosArrowDown
						style={{ alignSelf: 'center', marginLeft: 10, marginBottom: 5,color:cssVar.getPropertyValue('--secondaryGreen') }}
						size={15}
					/>
				}
			/>
		);
	};

	render = () => {
		return (
			<span style={{ ...this.props.containerStyle, zIndex: 200 }}>
				{this.renderSelectedItems()}
				{this.renderTextInput()}
				{this.renderFlatList()}
			</span>
		);
	};
	renderSelectedItems() {
		let items = this.props.selectedItems || [];
		if (items !== undefined && items.length > 0 && this.props.chip && this.props.multi) {
			return (
				<span
					style={{
						flexDirection: 'row',
						flexWrap: 'wrap',
						paddingBottom: 10,
						marginTop: 5
					}}
				>
					{items.map((item, index) => {
						return (
							<span
								key={index}
								style={{
									width: item.name.length * 8 + 60,
									justifyContent: 'center',
									flex: 0,
									backgroundColor: '#eee',
									flexDirection: 'row',
									alignItems: 'center',
									margin: 5,
									padding: 8,
									borderRadius: 15
								}}
							>
								<span style={{ color: '#555' }}>{item.name}</span>
								<span
									onClick={() =>
										setTimeout(() => {
											this.props.onRemoveItem(item, index);
										}, 0)}
									style={{
										backgroundColor: '#f16d6b',
										alignItems: 'center',
										justifyContent: 'center',
										width: 25,
										height: 25,
										borderRadius: 100,
										marginLeft: 10
									}}
								>
									<span>X</span>
								</span>
							</span>
						);
					})}
				</span>
			);
		}
	}
}
