const CustomDropDown = ({ items, placeholder, onChange, initial, label, height }) => {

  const [state, setstate] = React.useState(initial);

  return (
    <span>
      <SearchableDropDown
        defaultIndex={state.id}
        // colors={t.colors}
        // dStyle={s}
        // dTheme={t}
        onTextChange={(text) => console.log(text)}
        selectedItems={state}
        onItemSelect={(item) => {
          if (item.id !== -1) {
            setstate(item);
            onChange(item);
          }
        }}
        textInputStyle={{
          backgroundColor: 'transparent',
          fontSize: '14px',
          color: cssVar.getPropertyValue("--black"),
          borderWidth: 1
          //  width: widthPercentageToDP('100%'),
        }}
        itemStyle={{
          padding: "6px",
          borderTopRadius: 5,
          backgroundColor: cssVar.getPropertyValue("--lightest"),
        }}
        itemTextStyle={{
          color: cssVar.getPropertyValue("--black"),
          fontSize: '14px'
        }}
        itemsContainerStyle={{
          maxHeight: '160px',
          borderColor: cssVar.getPropertyValue("--secondaryGreen"),
          borderRadius: 5,
          borderBottomPadding: 10
        }}
        items={items}
        placeholder={placeholder}
        label={label}
        placeholderTextColor={cssVar.getPropertyValue("--silver")}
        resPtValue={false}
        height={height}
        underlineColorAndroid="transparent" />
    </span>
  );
};

export default CustomDropDown;
